import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import useMediaQuery from '@material-ui/core/useMediaQuery';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(7, 2),
    backgroundColor: '#252725',
    textAlign: 'center'
  },
  title: {
    fontSize: '3rem',
    color: '#cfcfcf',
    letterSpacing: '2px',
    textAlign: 'center',
    '&.mobile': {
      fontSize: '2rem'
    }
  },
  introText: {
    color: '#cfcfcf',
    textAlign: 'center',
    display: 'inline-block',
    width: '50%',
    fontSize: '1.4rem',
    fontWeight: 300,
    '&.mobile': {
      fontSize: '1rem',
      width: '100%'
    }
  },
  btn: {
    display: 'block',
    margin: 'auto',
    borderColor: '#ffd676',
    color: '#ffd676',
    marginTop: 30,
    '&:hover': {
      color: '#252725',
      backgroundColor: '#ffd676',
    }
  }
}));

export default function IntroContent() {
  const classes = useStyles();
  const isMobileView = useMediaQuery('(max-width:700px)');
  const h1Class = isMobileView ? `${classes.title} mobile` : classes.title
  const introText = isMobileView ? `${classes.introText} mobile` : classes.introText
  return (
    <div className={classes.root}>
      <Typography variant="h1" component="h1" className={h1Class}  gutterBottom>
        A platform to evaluate and grow your skills
      </Typography>
      <Typography variant="body1" className={introText} gutterBottom>
        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quos blanditiis tenetur
        unde suscipit, quam beatae rerum inventore consectetur, neque doloribus, cupiditate numquam.
      </Typography>
      <Button variant="outlined" className={classes.btn}>Evaluate Your Skills</Button>
    </div>
  )
}
