import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Link from '@material-ui/core/Link';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import LadderLogo from '../../images/ladder.png'
import DesktopMenu from '../Menus/desktopMenus'
import MobileMenu from '../Menus/mobileMenus'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    marginBottom: '67px'
  },
  mainLogo: {
    height: 64
  }
}));

export default function DefaultHeader() {
  const classes = useStyles();
  const isMobileView = useMediaQuery('(max-width:700px)');
  return (
    <div className={classes.root}>
      <AppBar position="fixed" color="default" style={{ paddingTop: 10, paddingBottom: 10 }}>
        <Toolbar>
          <Link href="#" color="inherit">
            <img src={LadderLogo} alt="logo" className={classes.mainLogo}/>
          </Link>
          {isMobileView ? (
            <MobileMenu />
          ) : (
            <DesktopMenu />
          )}
        </Toolbar>
      </AppBar>
    </div>
  );
}
