import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(3, 2),
    backgroundColor: '#252725',
    textAlign: 'center',
    width: '100%'
  },
  link: {
    display: 'inline-block',
    marginRight: '10px',
    textTransform: 'none',
    color: '#bbb'
  },
  copyRightInfo: {
    color: '#999'
  }
}));

export default function Footer() {
  const classes = useStyles();
  const dt = new Date();
  return (
    <div className={classes.root}>
      <Typography variant="overline" display="block" gutterBottom>
        <Link href="#" color="inherit" className={classes.link}>
          Terms and Conditions
        </Link>
        <Link href="#" color="inherit" className={classes.link}>
          Privacy Policy
        </Link>
      </Typography>
      <Typography variant="caption" display="block" gutterBottom className={classes.copyRightInfo}>
        Copyright &copy; {dt.getFullYear()} geeksladder.com, All rights reserved.
      </Typography>
    </div>
  );
}
