import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import MenuRoundedIcon from '@material-ui/icons/MenuRounded';
import CloseIcon from '@material-ui/icons/Close';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  nav: {
    marginLeft: 'auto'
  },
  menuWrapper: {
    listStyleType: 'none',
  },
  menu: {
    padding: 15,
    textAlign: 'right'
  },
  link: {
    fontWeight: 400
  },
  mainLink: {
    fontWeight: 500
  },
  menuIcon: {
    color: '#333',
    cursor: 'pointer'
  },
}));

export default function MobileMenu() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
    const toggleDrawer = () => {
      setOpen(!open)
    }
  return(
    <nav className={classes.nav}>
      <Link component="button" className={classes.menuIcon} onClick={toggleDrawer}>
        <MenuRoundedIcon />
      </Link>
      <SwipeableDrawer
        anchor="right"
        open={open}
        onClose={toggleDrawer}
        onOpen={toggleDrawer}
      >
        <Link component="button" className={classes.menuIcon} style={{ textAlign: 'right', margin: '20px' }} onClick={toggleDrawer}>
          <CloseIcon />
        </Link>
        <ul className={classes.menuWrapper}>
          <li className={classes.menu}>
            <Link href="#" color="inherit" className={classes.link}>
              Categories
            </Link>
          </li>
          <li className={classes.menu}>
            <Link href="#" color="inherit" className={classes.link}>
              Professionals
            </Link>
          </li>
          <li className={classes.menu}>
            <Link href="#" color="inherit" className={classes.mainLink}>
              Sign In
            </Link>
          </li>
          <li className={classes.menu}>
            <Link href="#" color="inherit" className={classes.mainLink}>
              Register
            </Link>
          </li>
        </ul>
      </SwipeableDrawer>
    </nav>
  )
}
