import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Container from '@material-ui/core/Container';

import Professional from '../../images/professional.png'
import Expert from '../../images/expert.jpg'
const useStyles = makeStyles(theme => ({
  root: {
    marginTop: 30,
    marginBottom: 30,
    flexGrow: 1,
  },
  card: {
    display: 'flex',
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    flex: '1 0 auto',
  },
  cover: {
    width: 300,
    backgroundSize: 'contain',
    backgroundPosition: 'top'
  },
  btn: {
    display: 'block',
    color: '#252725',
    marginTop: 30,
    backgroundColor: '#ffd676',
    '&:hover': {
      color: '#ffd676',
      backgroundColor: '#252725'
    }
  },
}))

export default function IntroContent() {
  const classes = useStyles();

  const cardComponent = type => {
    return (
      <Card className={classes.card}>
        <CardMedia
          className={classes.cover}
          image={type === 'expert' ? Expert : Professional}
          title={type}
        />
        <div className={classes.details}>
          <CardContent className={classes.content}>
            <Typography gutterBottom variant="h5" component="h2">
              {type === 'professional' ? 'Professional' : 'Expert' }
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quos blanditiis tenetur unde suscipit,
              quam beatae rerum inventore consectetur, neque doloribus, cupiditate numquam
            </Typography>
            <div>
              <List component="ul">
                <ListItem>
                  <ListItemIcon>
                    <ArrowForwardIcon />
                  </ListItemIcon>
                  <ListItemText primary="Lorem ipsum dolor sit amet, consectetur adipisicing elit." />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <ArrowForwardIcon />
                  </ListItemIcon>
                  <ListItemText primary="Lorem ipsum dolor sit amet, consectetur adipisicing elit." />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <ArrowForwardIcon />
                  </ListItemIcon>
                  <ListItemText primary="Lorem ipsum dolor sit amet, consectetur adipisicing elit." />
                </ListItem>
              </List>
            </div>
            <Button variant="outlined" className={classes.btn}>Register Your Skills</Button>
          </CardContent>
        </div>
      </Card>
    )
  }
  return (
    <div className={classes.root}>
      <Container maxWidth="xl">
        <Grid container spacing={3}>
          <Grid item xs>
            <Paper className={classes.expertWrapper}>
              {cardComponent('professional')}
            </Paper>
          </Grid>
          <Grid item xs>
            <Paper className={classes.professionalWrapper}>
              {cardComponent('expert')}
            </Paper>
          </Grid>
        </Grid>
      </Container>
    </div>
  )
}
